.header {
    height: 114px;
    background-color: $black;
    display: flex;
    align-items: center;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 11;

    .header__leftsect {
        padding-left: 15px;
        padding-right: 15px;
        max-width: 30%;
        flex: 0 0 30%;

        .header__phone {
            a {
                display: inline-block;
                width: 28px;
                margin-bottom: 20px;
                margin-top: 2px;

                img {
                    max-width: 100%;
                }
            }
        }
    }

    .header__centersect {
        padding-left: 15px;
        padding-right: 15px;
        max-width: 40%;
        flex: 0 0 40%;
        display: flex;
        align-items: center;
        justify-content: center;

        .header__logo {
            display: block;
            width: 100%;
            text-align: center;

            img {
                min-width: 110px;
                max-width: 125px;
                width: 100%;
            }
        }
    }

    .header__rightsect {
        padding-left: 15px;
        padding-right: 15px;
        max-width: 30%;
        flex: 0 0 30%;
        text-align: right;

        .header__social {
            margin-bottom: 25px;

            ul {
                @include inlinelist;
                display: inline-flex;
                align-items: center;
                justify-content: space-between;
                max-width: 70px;
                width: 100%;
            }
        }

        .header__lang {
            ul {
                @include inlinelist;

                li {
                    margin-right: 24px;
                    position: relative;

                    a {
                        color: $grey-dark;
                        font-size: 17px;
                        line-height: 20px;
                        font-family: Rawercondensedregular;
                    }

                    &:after {
                        content: '';
                        display: inline-block;
                        width: 1px;
                        height: 8px;
                        background: $yellow;
                        right: -13px;
                        position: absolute;
                        top: 8px;
                    }

                    &:last-child {
                        margin-right: 0;

                        &:after {
                            display: none;
                        }
                    }
                }

                li.active {
                    a {
                        color: $yellow;
                    }
                }
            }
        }
    }

    .header__burger {
        img {
            margin-left: -7px;
        }

        .burgericon {}

        .closemenu {
            display: none;
        }
    }
}

.header__menu,
.header__deskphone {
    display: none;
}

.header__burger {
    position: relative;
}

.header-icon.header-icon-hamburger {
    position: absolute;
    // top: 50%;
    // transform: translateY(-50%);
    margin-left: 0;
    width: 30px;
    height: 20px;
    // left: 1.00rem;
    cursor: pointer;
    opacity: 1;
    display: block;

    span {
        display: block;
        height: 2px;
        width: 100%;
        position: absolute;
        left: 0;
        transition: all .4s cubic-bezier(.165, .84, .44, 1);
        -webkit-transition: all .4s cubic-bezier(.165, .84, .44, 1);
        -moz-transition: all .4s cubic-bezier(.165, .84, .44, 1);
        -ms-transition: all .4s cubic-bezier(.165, .84, .44, 1);
        -o-transition: all .4s cubic-bezier(.165, .84, .44, 1);
        background: #FFD900;
        border-radius: 2px;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
        -ms-border-radius: 2px;
        -o-border-radius: 2px;

        &:nth-child(2) {
            top: 9px;
        }

        &:nth-child(3) {
            bottom: 0;
        }
    }

}

.header-icon.header-icon-hamburger.burger-transform {
    span {
        &:first-child {
            transform: translateY(9px) rotate(45deg);
            -webkit-transform: translateY(9px) rotate(45deg);
            -moz-transform: translateY(9px) rotate(45deg);
            -ms-transform: translateY(9px) rotate(45deg);
            -o-transform: translateY(9px) rotate(45deg);
        }

        &:nth-child(2) {
            opacity: 0;
            transform: translateX(-15px);
            -webkit-transform: translateX(-15px);
            -moz-transform: translateX(-15px);
            -ms-transform: translateX(-15px);
            -o-transform: translateX(-15px);
        }

        &:nth-child(3) {
            transform: translateY(-9px) rotate(-45deg);
            -webkit-transform: translateY(-9px) rotate(-45deg);
            -moz-transform: translateY(-9px) rotate(-45deg);
            -ms-transform: translateY(-9px) rotate(-45deg);
            -o-transform: translateY(-9px) rotate(-45deg);
        }
    }
}

@media(max-width:1199.8px) {
    .header__menu.show {
        opacity: 1;
        visibility: visible;
        display: flex;
    }

    .header__menu {
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        left: 0;
        top: 114px;
        height: calc(100vh - 114px);
        z-index: 1212;
        background: #000;
        padding: 40px 0;
        transition: .5s;
        opacity: 0;
        visibility: hidden;
        display: none;

        ul {
            width: 100%;
            margin: -70px 0 0 0;
            padding: 0;
            list-style-type: none;
            text-align: center;
            max-height: 285px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            li {
                a {
                    color: $white;
                    font-size: 20px;
                    line-height: 24px;
                    font-family: Rawercondensedbold;
                    display: block;
                    text-align: left;
                    padding: 0 22px;
                    transform: translateY(0);
                    -webkit-transform: translateY(0);
                    -moz-transform: translateY(0);
                    -ms-transform: translateY(0);
                    -o-transform: translateY(0);
                }
            }
        }
    }

    .header__menu.show {
        ul li {
            a {
                animation-name: phonemenu;
                animation-duration: 1s;
                opacity: 0;
                animation-fill-mode: forwards;
            }

            &:first-child a {
                animation-delay: .1s;
            }

            &:nth-child(2) a {
                animation-delay: .2s;
            }

            &:nth-child(3) a {
                animation-delay: .3s;
            }

            &:nth-child(4) a {
                animation-delay: .4s;
            }
        }
    }

    @keyframes phonemenu {
        from {
            opacity: 0;
            transform: translateY(15px);
            -webkit-transform: translateY(15px);
            -moz-transform: translateY(15px);
            -ms-transform: translateY(15px);
            -o-transform: translateY(15px);
        }

        to {
            opacity: 1;
            transform: translateY(0px);
            -webkit-transform: translateY(0px);
            -moz-transform: translateY(0px);
            -ms-transform: translateY(0px);
            -o-transform: translateY(0px);
        }
    }

}

@media(min-width:1200px) {
    body.main .header {
        background: rgba(0, 0, 0, .8);
    }

    .header {
        height: 110px;

        .header__leftsect {
            display: none;
        }

        .header__centersect {
            max-width: 145px;
            flex: 0 0 145px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }

        .header__rightsect {
            max-width: calc(100% - 145px);
            // flex: 0 0 calc(100% - 145px);
            flex: 0 0 100%;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            position: relative;
        }

        .header__menu,
        .header__deskphone {
            display: inline-block;
        }

        .header__menu {
            ul {
                list-style-type: none;
                padding: 0;
                margin: 0;

                li {
                    display: inline-block;
                    vertical-align: middle;
                }
            }
        }
    }

    .header .header__rightsect .header__social {
        display: inline-block;
        margin: 0;
    }

    .header__lang {
        display: inline-block;
    }

    .header__logo img {
        width: 113px;
        border: none;
    }

    .header .header__rightsect .header__lang ul li a {
        font-size: 18px;
        line-height: 22px;
        color: #828282;
        transition: .3s;

        &:hover {
            color: $white;
        }
    }

    .header .header__rightsect .header__lang ul li {
        margin-right: 36px;
    }

    .header .header__rightsect .header__lang ul li:after {
        right: -21px;
        height: 11px;
        background: $white;
        top: 7px;
    }

    .header .header__rightsect .header__lang ul li.active a {
        color: $white;
    }

    .header .header__rightsect .header__social {
        position: absolute;
        left: 70px;

        ul {
            li {
                a {
                    transition: .3s;
                    opacity: 1;

                    &:hover {
                        opacity: .5;
                    }
                }

                &:first-child {
                    margin-right: 35px;
                }
            }
        }

        svg path {
            fill: $white;
        }
    }

    .header__deskphone {
        margin-right: 60px;

        a {
            color: $white;
            font-size: 18px;
            line-height: 22px;
            font-family: Rawercondensedbold;
            letter-spacing: .5px;
            transition: .3s;

            &:hover {
                color: #AECCC2;
            }
        }
    }

    .header__menu {
        ul {
            li {
                margin-right: 60px;

                a {
                    color: $white;
                    font-size: 18px;
                    line-height: 22px;
                    font-family: Rawercondensedbold;
                    letter-spacing: .5px;
                    transition: .3s;

                    &:hover {
                        color: #AECCC2;
                    }
                }
            }
        }
    }


}


@media(min-width:1500px) and (max-width:1600px) {

    .header__deskphone,
    .header__menu ul li {
        margin-right: 40px;
    }
}

@media(min-width:1200px) and (max-width:1499.8px) {

    .header__deskphone,
    .header__menu ul li {
        margin-right: 25px;
    }

    .header .header__rightsect .header__social {
        left: 20px;
    }

    .header .header__rightsect .header__social ul li:first-child {
        margin-right: 15px;
    }

    .header__menu ul li a,
    .header__deskphone a {
        font-size: 16px;
    }

    .header__deskphone {
        min-width: 104px;
    }

    .header__lang {
        min-width: 77px;
    }
}




@media(max-height: 500px) {
    .header__menu {
        display: block;
        overflow: scroll;

        ul {
            height: 200px;
            margin: 0;
        }
    }
}