.menusection {
    padding: 44px 0 10px 0;

    .container {
        background-image: url('../img/ourmenu-bg.png');
        background-repeat: repeat-y;
        background-position: top center;
        background-size: 100% auto;
    }

    h1 {
        margin-bottom: 34px;
    }


}

.product {
    position: relative;
    text-align: center;
    padding-top: 100px;
    // height: 100%;
    margin-bottom: 46px;

    .product__pics {
        position: absolute;
        width: 100%;
        top: 0;
        min-height: 230px;

        .product__pic {
            width: 200px;
            height: 200px;
            overflow: hidden;
            border-radius: 50%;
            margin: 0 auto 5px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: 50% 50%;
            }
        }

        .product__flag {
            // width: 30px;
            width: 40px;
        }
    }

    .product__descr {
        background-color: $white;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.15);
        padding: 150px 40px 22px 40px;
        min-height: 245px;
        height: 100%;

        .product__title {
            font-family: Rawercondensedbold;
            font-size: 24px;
            line-height: normal;
            text-align: center;
            color: #010101;
            margin: 0 0 12px 0;
        }

        .product__description {
            font-size: 16px;
            line-height: 20px;
            color: #010101;
            margin: 0 0 18px 0;
        }

        .product__price {
            font-size: 20px;
            line-height: 24px;
            color: #010101;
            font-family: Rawercondensedbold;
            margin: 0;
        }

        .product__pricelist {
            padding: 0;
            margin: 0;
            list-style-type: none;

            li {
                display: block;
                text-align: center;
                font-size: 16px;
                line-height: 18px;
                color: #010101;
                font-family: Rawercondensedbold;
                margin-bottom: 10px;

                & span:first-child {
                    flex: 0 0 calc(100% - 48px);
                    max-width: calc(100% - 48px);
                    text-align: left;
                }

                & span:nth-child(2) {
                    flex: 0 0 48px;
                    max-width: 48px;
                    text-align: right;
                }
            }
        }

        .product__discover {
            height: 30px;
            background: #68B59A;
            color: $white;
            font-size: 13px;
            line-height: 24px;
            font-family: Rawercondensedregular;
            display: flex;
            align-items: center;
            justify-content: center;
            // position: absolute;
            // bottom: 12px;
            // left: 0;
            // right: 0;
            // max-width: calc(100% - 30px);
            margin: 0 auto;
            // -
            position: relative;
            bottom: initial;
            max-width: 108px;
            left: initial;
            right: initial;
            margin-top: 18px;

            span {
                &:after {
                    content: '';
                    display: inline-block;
                    width: 12px;
                    height: 8px;
                    vertical-align: middle;
                    background-image: url('../img/arrow-btn.svg');
                    background-size: 100% 100%;
                    margin-left: 8px;
                }
            }
        }
    }
}



@media(min-width:768px) {
    .menusection .container {
        background-image: url('../img/ourmenu-tablet-bg.png');
    }

    .product {
        height: 100%;
        margin-bottom: 0;
    }

    .menusection .row .col-md-6 {
        margin-bottom: 45px;
    }

    .product .product__descr {
        padding-bottom: 60px;
    }

    .product .product__descr .product__discover {
        margin-top: 0;
        position: absolute;
        left: 50%;
        //right: 0;
        bottom: 22px;
        transform: translateX(-50%);
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        width: 100%;
    }

}

@media(min-width:992px) {
    .product .product__descr {
        padding: 110px 40px 60px;
    }
}

.ourmenu-parallax {
    display: none;
}

@media(min-width:1025px) {

    main {
        padding-top: 110px;
    }

    .menusection {
        padding: 120px 0 60px 0;

        h1 {
            font-size: 48px;
            line-height: 58px;
            padding-left: 30px;
            border: none;
            margin-bottom: 110px;

            &:after {
                content: '';
                display: inline-block;
                width: 2px;
                height: 32px;
                background: $black;
                position: absolute;
                left: 0;
                top: 55%;
                transform: translateY(-50%);
            }
        }
    }

    .product .product__pics {
        min-height: 186px;
    }

    .product {
        padding-top: 67px;
    }

    .product .product__pics .product__pic {
        width: 140px;
        height: 140px;
    }

    .product .product__descr {
        padding: 139px 45px 70px 45px;
        min-height: 364px;
    }

    .product .product__pics .product__pic {
        margin-bottom: 34px;
    }

    .product .product__descr .product__title {
        font-size: 22px;
        line-height: 27px;
        margin-bottom: 21px;
    }

    .product .product__descr .product__pricelist li {
        margin-bottom: 12px;
        font-size: 18px;
        line-height: 18px;
    }

    .product .product__descr .product__discover {
        bottom: 25px;
        max-width: 150px;
        height: 36px;
        font-size: 15px;

        span {
            transition: .3s;
            opacity: 1;
        }

        &:hover {
            span {
                opacity: .7;
            }
        }
    }

    .menusection .container {
        background-image: none;
    }

    .product .product__descr .product__pricelist li span:nth-child(2) {
        flex: 0 0 60px;
        max-width: 60px;
    }

    .product .product__descr .product__pricelist li span:first-child {
        flex: 0 0 calc(100% - 60px);
        max-width: calc(100% - 60px);
    }

    .menusection .row .col-6 {
