@font-face {
    font-family: Rawercondensedregular;
    src: url('../fonts/Rawercondensedregular.otf');
}

@font-face {
    font-family: Rawercondensedbold;
    src: url('../fonts/Rawercondensedbold.otf');
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: 'Raleway', sans-serif;
}

a,
a:hover,
a:focus {
    outline: none;
    text-decoration: none;
}

main {
    padding-top: 114px;
    min-height: calc(100vh - 40px);
    display: block;
}

h1 {
    font-family: Rawercondensedbold;
    font-size: 30px;
    line-height: 36px;
    color: $black;
    margin: 0;
    padding-left: 24px;
    position: relative;

    &:after {
        content: "";
        display: inline-block;
        width: 2px;
        height: calc(100% - 10px);
        background: #000;
        position: absolute;
        left: 0;
        top: 54%;
        transform: translateY(-50%);
    }
}


// index
.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-pagination-bullet {
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 12px;
    color: #000;
    opacity: 1;
    background: rgba(0, 0, 0, 0.2);
}

.swiper-pagination-bullet-active {
    color: #fff;
    background: #007aff;
}

.mainslider {
    position: fixed;
    left: 0;
    top: 114px;
    width: 100%;
    height: calc(100vh - 114px);

    .mainslider__wrapper {
        position: relative;
        height: 100%;
        width: 100%;
    }

    .swiper-slide {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 50% 50%;
        }
    }

    .swiper-button-next,
    .swiper-button-prev {
        width: 30px;
        height: 58px;
        background-color: rgba(0, 0, 0, .5);
        background-repeat: no-repeat;
        background-position: center center;
        top: 27vh;
        margin: 0;
        transition: opacity .5s, visibility .5s;
        opacity: 1;
        visibility: visible;
        outline: none;
        display: flex;
        align-items: center;
        justify-content: center;

        &:after {
            display: none;
        }
    }

    .swiper-button-next {
        right: 0;
        // background-image: url('../img/arrow-right.svg');
    }

    .swiper-button-prev {
        left: 0;
        // background-image: url('../img/arrow-left.svg');
    }

    .swiper-pagination {
        top: 49vh;
        transition: opacity .5s, visibility .5s;
        opacity: 1;
        visibility: visible;

        .swiper-pagination-bullet {
            width: 9px;
            height: 9px;
            background: rgba(0, 0, 0, 0.6);
            transition: .3s;
        }

        .swiper-pagination-bullet-active {
            background: #FFFFFF;
        }
    }

    .swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0 7px;
    }
}

.mainnav {
    padding: 0 30px 25px;
    position: relative;
    top: 54vh;

    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;

        li {
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;
            }

            a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100px;
                background: rgba(0, 0, 0, 0.5);
                font-family: Rawercondensedbold;
                font-size: 24px;
                line-height: 29px;
                color: $white;
                position: relative;

                span {
                    position: relative;
                    z-index: 2;
                }

                &:after {
                    content: '';
                    display: inline-block;
                    width: calc(100% - 20px);
                    height: calc(100% - 20px);
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    background: rgba(51, 51, 51, 0.6);
                }
            }
        }
    }
}

.hideel {
    opacity: 0 !important;
    visibility: hidden !important;
}

@media(min-width:768px) {
    .mainnav {
        position: absolute;
        width: 100%;
        top: initial;
        bottom: 100px;
        padding: 0 30px;

        ul {
            display: flex;
            justify-content: space-between;

            li {
                margin-bottom: 0;
                width: 30%;
            }
        }
    }

    .mainslider {
        .swiper-pagination {
            top: initial;
            bottom: 20px;
        }

        .swiper-button-next,
        .swiper-button-prev {
            top: 33vh;
            width: 50px;
            height: 96px;
        }
    }
}

@media(min-width:1025px) {
    .mainslider {
        height: 100vh;
        top: 0;
    }

    .mainslider .swiper-button-next,
    .mainslider .swiper-button-prev {
        top: 50%;
        transform: translateY(-50%);

        svg rect {
            transition: .3s;
        }

        &:hover {
            svg rect {
                fill: #AECCC2;
            }
        }
    }

    .mainslider .swiper-button-next svg,
    .mainslider .swiper-button-prev svg {
        width: 19px;
        height: 34px;
    }

    .mainnav {
        bottom: 120px;
        max-width: 1140px;
        margin: 0 auto;
        padding: 0;
        right: 0;
        left: 0;
    }

    .mainslider .swiper-pagination .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        transition: .5s;
    }

    .mainnav ul li a {
        height: 160px;
        background: rgba(0, 0, 0, 0.5);
        font-size: 30px;
        line-height: 36px;
        transition: .5s;
        z-index: 2;

        &:after {
            background: rgba(51, 51, 51, 0.6);
            width: calc(100% - 40px);
            height: calc(100% - 50px);
        }

        &:hover {
            background: rgba(0, 0, 0, 0.7);
            color: #68B59A;

            &:after {
                background: rgba(51, 51, 51, 0.7);
            }
        }
    }

}






@media(min-width:1440px) {
    .header .container {
        max-width: 100%;
        padding-left: 100px;
        padding-right: 100px;
    }

    .mainslider .swiper-button-prev {
        left: 100px;
    }

    .mainslider .swiper-button-next {
        right: 100px;
    }

}















