.product-sect {
    overflow: hidden;
}

.product-secpicbox {
    text-align: right;
    padding-top: 15px;
    position: relative;

    .prodbgpic {
        position: absolute;
        width: auto;
        top: 25px;
        z-index: 2;
        right: 0;
        max-width: 100%;
    }

    .product-sect__pic {
        width: 290px;
        height: 290px;
        overflow: hidden;
        border-radius: 50%;
        border: 1px solid #DBD0B5;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        top: -32px;
        position: relative;
        right: -32px;
        border-right: none;
        border-top: none;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 50% 50%;
            border-radius: 50%;
            width: 258px;
            height: 258px;
        }
    }
}

.product-sectextbox {
    h1 {
        border: none;
        padding: 0;
        margin: 0 0 24px 0;
        font-size: 26px;
        line-height: 32px;
    }

    p {
        font-size: 16px;
        line-height: 20px;
        color: #333333;
        margin: 0;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.species {
    margin-top: 45px;
    padding-bottom: 40px;
    background-image: url('../img/prodbg2.png');
    background-repeat: no-repeat;
    background-position: top center;
    background-size: 100% auto;

    .species__box {
        margin-bottom: 35px;

        h3 {
            color: #010101;
            font-size: 26px;
            line-height: 32px;
            font-family: Rawercondensedbold;
            margin: 0 0 24px 0;
        }

        .species__block {
            padding: 40px 25px 35px 25px;
            background-image: url('../img/species-bg.png');
            background-size: cover;

            .title {
                font-size: 20px;
                line-height: 24px;
                color: #010101;
                font-family: Rawercondensedbold;
                margin: 0 0 30px 0;
                padding-left: 35px;
                position: relative;

                &:after {
                    content: '';
                    display: inline-block;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 25px;
                    height: 25px;
                    background-image: url('../img/cookbook.svg');
                    background-repeat: no-repeat;
                    background-position: center center;
                }
            }

            .descr {
                font-size: 16px;
                line-height: 24px;
                color: #333333;
                margin: 0;
            }
        }
    }
}

.prepared__titlebox {
    padding: 0 40px 21px 40px;


    .title {
        font-size: 30px;
        line-height: 36px;
        color: #000000;
        font-family: Rawercondensedbold;
        margin: 0 0 24px 0;
    }

    .descr {
        font-family: Rawercondensedregular;
        font-weight: 250;
        font-size: 20px;
        color: #4F4F4F;
        line-height: 18px;
        margin: 0;
        padding-bottom: 21px;
        border-bottom: 1px solid #E0E0E0;
    }
}

.prepared__picbox {
    img {
        max-width: 100%;
        width: 100%;
        margin-bottom: 35px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.prepared__bottsect {
    padding: 34px 40px 96px;
    background-image: url('../img/prodbg1.png');
    background-repeat: no-repeat;
    background-position: left bottom;

    .title {
        font-size: 20px;
        line-height: 28px;
        color: #000000;
        margin: 0 0 30px 0;
        font-family: Rawercondensedbold;
    }

    .descr {
        font-size: 16px;
        line-height: 24px;
        color: #010101;
        font-family: Rawercondensedbold;
        margin: 0 0 16px 0;
    }

    &>a {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background: #010101;
        font-size: 16px;
        line-height: 24px;
        color: #FFFFFF;
        font-family: Rawercondensedregular;
        width: 140px;
        height: 40px;

        span {
            img {
                margin-left: 15px;
                vertical-align: middle;
            }
        }
    }
}

.bottomfixed-nav {
    background: #010101;
    height: 40px;
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;

    .backpage {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 130px;
        height: 41px;
        bottom: 39px;
        left: 0;
        right: 0;
        margin: 0 auto;
        background: url('../img/fixednav.png') no-repeat;
        background-size: 100% 100%;
        background-position: top center;
        position: absolute;
        color: $white;
        font-size: 14px;
        line-height: 17px;
        font-family: Rawercondensedregular;

        img {
            margin-right: 4px;
            display: inline-block;
            position: relative;
            top: 1px;
        }
    }
}

.prepared {
    .container {
        padding-left: 0;
        padding-right: 0;
    }
}

.back-desktop {
    display: none;
}

.prodbgpic-desktop {
    display: none;
}

.product-sectextbox h1:after {
    display: none;
}

.back-desktop.mob.container {
    display: block;
    margin-top: 30px;

    a {
        display: flex;
        align-items: center;
        justify-content: flex-start;

        img {
            margin-right: 5px;
        }
    }
}

@media(min-width:576px) {
    .prepared {
        .container {
            padding-left: 15px;
            padding-right: 15px;
        }
    }
}

@media(min-width:768px) {
    .product-sect .row>.col-md-6:first-child {
        order: 2;
    }

    .product-sect .row>.col-md-6:nth-child(2) {
        order: 1;
    }

    .product-secpicbox .product-sect__pic img {
        width: 320px;
        height: 320px;
    }

    .product-secpicbox .product-sect__pic {
        width: 350px;
        height: 350px;
    }

    .product-sectextbox {
        padding-top: 40px;
    }

    .product-sectextbox h1 {
        font-size: 48px;
        line-height: 58px;
    }

    .product-sectextbox .descr {
        font-size: 18px;
        line-height: 30px;
    }

    .species {
        margin-top: 15px;
    }

    .species .species__box h3 {
        font-size: 28px;
        line-height: 34px;
    }


}

#scene {
    overflow: hidden;
}

.product-parallax {
    display: none;
}

@media(min-width:1025px) {

    .product-parallax {
        display: block;
        position: absolute !important;
        left: 0;
        top: 0;
        width: 100%;
        height: 700px;
        z-index: 2;

        >div {
            position: absolute !important;

            &:first-child {
                left: 26% !important;
                top: 19% !important;
            }

            &:nth-child(2) {
                left: 50% !important;
                top: 30% !important;
            }

            &:nth-child(3) {
                left: 48% !important;
                top: 57% !important;
            }

            &:nth-child(4) {
                left: 47% !important;
                top: 60% !important;
            }

            &:nth-child(5) {
                left: 53% !important;
                top: 66% !important;
            }

            &:nth-child(6) {
                left: 55% !important;
                top: 77% !important;
            }

            &:nth-child(7) {
                left: 30% !important;
                top: 81% !important;
            }

            &:nth-child(8) {
                left: 21% !important;
                top: 87% !important;
            }
        }
    }


    .product-wrapper {
        position: relative;
    }

    .back-desktop.mob.container {
        display: none;
    }

    .back-desktop {
        display: block;
    }

    .back-desktop a {
        display: inline-block;
        cursor: pointer;
        font-size: 20px;
        line-height: 24px;
        color: #828282;
        font-family: Rawercondensedregular;

        img {
            vertical-align: middle;
        }
    }

    .product-wrapper {
        padding-top: 88px;
    }

    .product-sectextbox {
        padding-top: 158px;
        height: 100%;
        // background-image: url('../img/bg2.png');
        // background-repeat: no-repeat;
        // background-position: top 25px left;
        background-image: none;
    }

    .product-secpicbox .product-sect__pic img {
        width: 580px;
        height: 580px;
    }

    .product-secpicbox .product-sect__pic {
        width: 578px;
        height: 578px;
        max-width: 100%;
        max-height: 100%;
        overflow: initial;
    }

    .product-secpicbox .prodbgpic {
        display: none;
    }

    .product-secpicbox {
        padding-top: 0;
    }

    .species {
        margin-top: 40px;
    }

    .product-sectextbox h1 {
        margin-bottom: 45px;
    }

    .species .species__box h3 {
        padding-left: 23px;
        margin-bottom: 12px;
    }

    .species .species__box .species__block {
        padding: 44px 40px 40px 42px;
    }

    .species .species__box .species__block .title:after {
        width: 29px;
        height: 31px;
        background-size: 100% auto;
    }

    .bottomfixed-nav {
        display: none;
    }

    .species .species__box .species__block .title {
        padding-left: 50px;
    }

    .species {
        background-image: none;
    }

    .prodbgpic-desktop {
        display: inline-block;
        position: absolute;
        z-index: 1;
        left: 14px;
        top: 95px;
    }

    .species .species__box {
        margin-bottom: 0;
    }

    .species {
        padding-bottom: 150px;
        background-image: url('../img/bg3.png');
        background-repeat: no-repeat;
        background-position: left 54% bottom 58px;
        background-size: 170px;
    }

    .prepared__titlebox {
        padding-left: 0;
    }


    .prodbgpic-desktop {
        display: none;
    }
}

@media(min-width:1025px) and (max-width:1199.8px) {
    .species .species__box {
        margin-bottom: 30px;
    }
}

// font-family: Rawercondensedregular;
// font-family: Rawercondensedbold;


@media(min-width:1440px) {
    .back-desktop.container {
        max-width: 100%;
        padding-left: 100px;
        padding-right: 100px;
    }
}