.thanks-wrapper {
    height: calc(100vh - 154px);
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('../img/thankbg.png');
    background-repeat: no-repeat;
    background-size: 100%;
    min-height: 350px;

    .centerbox {
        text-align: center;
    }

    h1 {
        padding: 0;
        font-size: 36px;
        line-height: 64px;
        margin: 0 0 5px 0;

        &:after {
            display: none;
        }
    }

    p {
        max-width: 255px;
        color: $grey;
        font-size: 18px;
        line-height: 28px;
        font-family: Rawercondensedbold;
        margin: 0 auto 40px;
    }

    a {
        display: flex;
        height: 46px;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        outline: none;
        font-size: 16px;
        line-height: 19px;
        font-family: Rawercondensedbold;
        width: 278px;
    }

    .tohomelink {
        margin: 0 0 20px 0;
        background: $light-green;
        color: $white;
    }

    .thanksback {
        background: #EDF7F4;
        color: #68B59A;
    }
}

.thanks-parallax {
    display: none;
}

@media(min-width:576px) {
    .thanks-wrapper {
        background-size: auto;
        background-position: top right;
    }
}

@media(min-width:768px) {
    .thanks-wrapper {
        background-image: url('../img/thanksbg2.png');
        background-position: center;
    }
}

@media(min-width:1025px) {
    .thanks-wrapper {
        h1 {
            font-size: 48px;
            line-height: 64px;
            margin-bottom: 17px;
        }

        p {
            max-width: 400px;
            width: 100%;
            font-size: 28px;
            line-height: 40px;
            margin-bottom: 45px;
        }

        .tohomelink {
            margin: 0 auto 20px;
            transition: .3s;
            -webkit-transition: .3s;
            -moz-transition: .3s;
            -ms-transition: .3s;
            -o-transition: .3s;

            &:hover {
                opacity: .8;
            }
        }

        .thanksback {
            margin: 0 auto;
            cursor: pointer;
            transition: .3s;
            -webkit-transition: .3s;
            -moz-transition: .3s;
            -ms-transition: .3s;
            -o-transition: .3s;

            &:hover {
                color: #AECCC2;
            }
        }
    }

    .thanks-wrapper {
        background-image: none;
        position: relative;
    }

    .thanks-parallax {
        position: absolute !important;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;

        >div {
            position: absolute !important;

            &:nth-child(1) {
                left: 21% !important;
                top: 27% !important;
            }

            &:nth-child(2) {
                left: 61% !important;
                top: 14% !important;
            }

            &:nth-child(3) {
                left: 79% !important;
                top: 33% !important;
            }

            &:nth-child(4) {
                left: 39% !important;
                top: 43% !important;
            }

            &:nth-child(5) {
                left: 26% !important;
                top: 71% !important;
            }

            &:nth-child(6) {
                left: 14% !important;
                top: 58% !important;
            }

            &:nth-child(7) {
                left: 22% !important;
                top: 68% !important;
            }

            &:nth-child(8) {
                left: 65% !important;
                top: 76% !important;
            }

            &:nth-child(9) {
                left: 77% !important;
                top: 65% !important;
            }

            &:nth-child(10) {
                left: 77% !important;
                top: 53% !important;
            }
        }
    }
}