.concept__topsect {
    position: relative;
    text-align: right;

    .concept__topsect-pagetitle {
        position: absolute;
        left: 15px;
        top: 44px;
        width: 187px;
        z-index: 2;
        text-align: left;
        padding-left: 25px;

        &:after {
            content: "";
            display: inline-block;
            width: 2px;
            position: absolute;
            left: 0;
            background-color: #fff;
            height: calc(100% - 14px);
            top: 51%;
            transform: translateY(-50%);
        }

        p {
            margin: 0;
            font-size: 30px;
            line-height: 36px;
            color: $white;
            font-family: Rawercondensedbold;
        }
    }

    &:after {
        content: '';
        display: block;
        background: linear-gradient(180deg, #FEDC00 0%, #E99600 100%);
        height: 730px;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }

    .babouchka {
        position: relative;
        width: auto;
        max-width: 100%;
        right: 0;
        top: 15px;
        z-index: 2;
    }

    .concept__topsectinfo {
        width: calc(100% - 15px);
        background-color: $white;
        padding: 48px 25px 0 25px;
        position: relative;
        z-index: 2;
        display: inline-block;
        text-align: left;
        padding-bottom: 24px;

        p:not(.title) {
            color: $text-color;
            font-size: 16px;
            line-height: 23px;
            margin: 0 0 20px 0;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .title {
            font-family: Rawercondensedbold;
            color: $black;
            font-size: 24px;
            line-height: 36px;
            margin: 0 0 21px 0;
        }

        .desc-r {
            height: 230px;
            overflow: hidden;
            position: relative;

            &:after {
                content: '';
                display: block;
                width: 100%;
                height: 120px;
                background: linear-gradient(178.45deg, rgba(255, 255, 255, 0) 62.54%, #FFFFFF 98.58%);
                position: absolute;
                left: 0;
                bottom: 0;
                z-index: 2;
            }
        }

        .learnmore-text {
            margin-top: 30px;

            p {
                margin: 0;
                font-size: 16px;
                line-height: 19px;
                color: #68B59A;
                font-family: Rawercondensedregular;

                .sl {
                    display: none;
                }
            }
        }
    }
}

.concept__topsect .concept__topsectinfo .desc-r.showt {
    height: 100%;

    &:after {
        display: none;
    }
}

.concept__val {
    padding: 50px 0 36px 0;

    .cval__title {
        color: $black;
        font-size: 30px;
        line-height: 36px;
        font-family: Rawercondensedbold;
        margin: 0 0 60px 0;
        padding-left: 26px;
        position: relative;

        &:after {
            content: '';
            display: inline-block;
            width: 2px;
            background: $black;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            height: calc(100% - 10px);
        }
    }
}

.cval__box {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 36px;

    .cval__boxpic {
        flex: 0 0 70px;
        max-width: 70px;
        padding: 10px 0 0 0;

        img {
            filter: drop-shadow(0px 4px 20px rgba(104, 181, 154, 0.25));
        }
    }

    .cval__boxinfo {
        flex: 0 0 calc(100% - 70px);
        max-width: calc(100% - 70px);
        padding-left: 28px;
        position: relative;

        .cval__boxdescr {
            position: relative;
            background-color: $white;
            border-radius: 10px;
            padding: 16px 25px;
            box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);

            &:after {
                content: "";
                display: inline-block;
                width: 24px;
                height: 24px;
                background-image: url('../img/value-figure.svg');
                position: absolute;
                left: -21px;
                background-repeat: no-repeat;
                background-position: center right;
                background-size: 100% 100%;
                top: 32px;
            }

            .title {
                margin: 0 0 10px 0;
                color: $grey;
                font-size: 18px;
                line-height: 24px;
                font-family: Rawercondensedbold;
            }

            .clickhere {
                display: inline-block;
                color: $light-green;
                font-size: 14px;
                line-height: 18px;
            }

            .descr {
                color: $light-green;
                font-size: 14px;
                line-height: 18px;
                margin: 0;
                display: none;
            }
        }
    }
}

.yellow-text {
    color: #FFC90B;
}

.concept__val .row .col-12:nth-child(even) {
    .cval__boxinfo {
        order: 1;
        padding: 0 28px 0 0;

        .cval__boxdescr:after {
            left: initial;
            right: -21px;
            transform: rotate(180deg);
        }
    }

    .cval__boxpic {
        order: 2;
    }
}

.weare {
    background: linear-gradient(180deg, #FCA0BD 0%, #FDA0BD 100%);
    position: relative;

    .logobg {
        position: absolute;
        left: 0;
        right: 0;
        top: 29px;
        margin: 0 auto;
    }

    .weare__wrap {
        padding: 85px 0 360px 0;
        background-image: url('../img/babouchka2.png');
        background-repeat: no-repeat;
        background-position: center bottom;
    }

    .title {
        font-size: 30px;
        line-height: normal;
        color: $white;
        font-family: Rawercondensedbold;
        margin: 0 0 60px 0;
        padding-left: 26px;
        position: relative;

        &:after {
            content: "";
            display: inline-block;
            width: 2px;
            background: $white;
            left: 0;
            position: absolute;
            height: calc(100% - 10px);
            top: 6px;
        }
    }
}

.weare__box {
    padding: 30px 25px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.12);
    margin-bottom: 15px;


    .weare__boxtitle {
        margin: 0 0 10px 0;
        font-size: 20px;
        line-height: 18px;
        font-weight: bold;
        color: $white;
    }

    .weare__boxdescr {
        margin: 0;
        color: $white;
        font-size: 14px;
        line-height: 18px;
        max-width: 223px;
    }
}

.yellow-box {
    background: #FFD900;
    background-image: url('../img/care.svg');
    background-position: center right 16px;
    background-repeat: no-repeat;
}

.green-box {
    background: #72BDA3;
    background-image: url('../img/letter.svg');
    background-position: center right 16px;
    background-repeat: no-repeat;
}

.blue-box {
    background: #009CD0;
    background-image: url('../img/earth.svg');
    background-position: center right 16px;
    background-repeat: no-repeat;
}

.concept__topsect .container {
    padding-left: 0;
    padding-right: 0;
}

.babouchka-desktop {
    display: none;
}

.concept__val {
    background-image: url('../img/bg4.png');
    background-size: 100% auto;
    background-position: top -55px center;
}

.weare .weare__wrap {
    background-size: 100% auto;
}


@media(min-width:400px) {
    .weare .weare__wrap {
        background-size: auto 400px;
    }

}

@media(min-width:576px) {
    .concept__topsect .concept__topsect-pagetitle {
        width: 250px;

        p br {
            display: none;
        }
    }

    .concept__topsect:after {
        display: none;
    }

    .concept__topsect .babouchka {
        display: none;
    }



    .concept__topsect {
        background-image: url('../img/babouchka3.png');
        background-repeat: no-repeat;
        background-position: center top;
        background-size: 100% auto;
        background-size: auto 400px;
        padding-top: 400px;
    }

    .concept__topsect .concept__topsectinfo {
        margin-top: -70px;
    }


}

@media(min-width: 768px) {
    .concept__topsect .container {
        padding-left: 0;
        padding-right: 0;
    }

    .concept__topsect {
        padding-top: 0;
        background-image: none;

    }

    .babouchka-desktop {
        display: block;
        max-width: 100%;
        z-index: 2;
        position: relative;
        width: 100%;
    }

    .cval__box .cval__boxpic {
        padding-top: 0;
        flex: 0 0 100%;
        max-width: 100%;
        text-align: center;
        margin-bottom: 50px;
    }

    .cval__box .cval__boxinfo {
        flex: 0 0 100%;
        max-width: 100%;
        padding-left: 0;
        text-align: center;
    }

    .concept__val .row .col-12:nth-child(2n) .cval__boxpic {
        order: initial;
    }

    .concept__val .row .col-12:nth-child(2n) .cval__boxinfo {
        order: initial;
        padding: 0;
    }

    .cval__box .cval__boxinfo .cval__boxdescr:after {
        display: none;
    }

    .cval__box .cval__boxinfo .cval__boxdescr {
        background-color: transparent;
        border-radius: 0;
        padding: 0;
        box-shadow: none;
    }

    .cval__box {
        padding: 72px 25px 33px 25px;
        background: #FFFFFF;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.08);
        margin-bottom: 0;
        height: 100%;
        display: block;
    }

    .cval__box .cval__boxinfo .cval__boxdescr .clickhere {
        display: none !important;
    }

    .cval__box .cval__boxinfo .cval__boxdescr .descr {
        display: block !important;
    }

    .concept__val .col-md-4 {
        margin-bottom: 30px;
    }

    .concept__topsect .concept__topsectinfo .learnmore-text {
        display: none;
    }

    .concept__topsect .concept__topsectinfo .desc-r {
        height: auto;
    }

    .concept__topsect .concept__topsectinfo .desc-r:after {
        display: none;
    }


}

@media(min-width: 1025px) {
    .concept__topsect .concept__topsect-pagetitle p {
        font-size: 48px;
        line-height: 64px;
    }

    .concept__topsect .concept__topsect-pagetitle {
        width: 400px;
        top: 120px;
    }

    .picwr {
        max-height: 100vh;
        overflow: hidden;
    }

    .concept__topsect .concept__topsectinfo {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        padding: 95px 0 63px 0;
        margin-top: -130px;
    }

    .concept__topsectinfo:after {
        content: "";
        display: block;
        position: absolute;
        background-color: #fff;
        height: 100%;
        width: 120vw;
        top: 0;
        left: -148px;
        z-index: 0;
    }

    .concept__topsect {
        overflow: hidden;
    }

    .concept__topsect .concept__topsectinfo .title {
        flex: 0 0 50%;
        max-width: 50%;
        z-index: 2;
        font-size: 36px;
        line-height: 48px;
        padding-right: 15%;
    }

    .concept__topsect .concept__topsectinfo .desc-r {
        flex: 0 0 50%;
        max-width: 50%;
        z-index: 2;
    }

    .concept__topsect .concept__topsectinfo p:not(.title) {
        font-size: 16px;
        line-height: 24px;
    }

    .concept__val {
        padding-top: 20px;
    }

    .concept__val .cval__title {
        font-size: 48px;
        line-height: 64px;
        margin-bottom: 40px;
    }

    .concept__val .cval__title:after {
        height: calc(100% - 30px);
        top: 52%;
    }

    .cval__box .cval__boxpic img {
        width: 120px;
    }

    .cval__box {
        padding: 50px 40px;
    }

    .cval__box .cval__boxpic {
        margin-bottom: 45px;
    }

    .cval__box .cval__boxinfo .cval__boxdescr .title {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 18px;
    }

    .cval__box .cval__boxinfo .cval__boxdescr .descr {
        font-size: 16px;
        line-height: 20px;
    }

    .concept__val {
        padding-bottom: 86px;
    }

    .weare .logobg {
        top: 70px;
        right: initial;
        left: 15px;
        width: 247px;
    }

    .weare .weare__wrap {
        padding: 147px 0 98px;
    }

    .weare .title {
        font-size: 48px;
        line-height: normal;
        margin-bottom: 100px;
    }

    .weare .titcont {
        position: relative;
    }

    .weare .title:after {
        height: calc(100% - 24px);
        top: 16px;
    }

    .weare__box {
        max-width: 415px;
        padding: 52px 48px;
        background-size: 120px;
    }

    .weare__box .weare__boxtitle {
        font-size: 22px;
        margin-bottom: 20px;
    }

    .weare__box .weare__boxdescr {
        font-size: 16px;
        line-height: 20px;
        max-width: initial;
    }

    .weare .weare__wrap {
        // background-size: auto 100%;
        background-size: auto 73%;
        background-position: right bottom;
    }

    .concept__val {
        background-image: none;
        position: relative;

        &:after {
            content: '';
            display: inline-block;
            width: 330px;
            height: 280px;
            background-image: url('../img/bg5.png');
            background-size: 100% 100%;
            background-repeat: no-repeat;
            position: absolute;
            right: 30%;
            top: -75px;
            z-index: -1;
        }
    }

}




