$black: #000;
$white: #fff;
$grey-dark: #4F4F4F;
$grey: #333333;
$yellow: #FFD900;
$orange: #E99600;
$text-color: #4F4F4F;
$light-green: #68B59A;



@mixin inlinelist {
    padding: 0;
    margin: 0;
    list-style-type: none;

    li {
        display: inline-block;
        vertical-align: middle;
    }
}


