.contact-topsect {
    padding: 44px 0 0 0;
    background-image: url('../img/bg6.png');
    background-repeat: no-repeat;
    background-position: left top 40px;

    .container {
        padding: 0;
        max-width: 100%;

        .row {
            margin-left: 0;
            margin-right: 0;
        }
    }

    h1 {
        margin-bottom: 34px;
        width: calc(100% - 30px);
        margin-left: 15px;
    }
}

.caontactform {
    padding: 40px 26px 22px 26px;
    background: $white;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
    z-index: 3;
    position: relative;

    .caontactform__title {
        font-size: 30px;
        line-height: 36px;
        color: #010101;
        font-family: Rawercondensedbold;
        margin: 0 0 42px 0;
    }

    .inputfield {
        position: relative;

        input {
            width: 100%;
            border: none;
            height: 40px;
            border-bottom: 1px solid #BDBDBD;
            padding: 0;
            outline: none;
            margin-bottom: 32px;
            color: #828282;
            font-weight: 300;
            font-size: 14px;
            line-height: 24px;
            font-family: Raleway, sans-serif;

            &::placeholder {
                color: #828282;
                font-weight: 300;
                font-size: 14px;
                font-family: Raleway, sans-serif;
            }
        }

        textarea {
            width: 100%;
            height: 110px;
            resize: none;
            outline: none;
            border: none;
            border-bottom: 1px solid #BDBDBD;
            color: #828282;
            font-weight: 300;
            font-size: 14px;
            line-height: 24px;
            font-family: Raleway, sans-serif;
            margin-bottom: 38px;

            &::placeholder {
                color: #828282;
                font-weight: 300;
                font-size: 14px;
                line-height: 24px;
                font-family: Raleway, sans-serif;
            }
        }


    }

    input[type="submit"] {
        width: 100%;
        border: none;
        outline: none;
        height: 46px;
        background: #68B59A;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $white;
        font-size: 16px;
        line-height: 19px;
        font-family: Rawercondensedbold;
        cursor: pointer;
        transition: .3s;
        -webkit-transition: .3s;
        -moz-transition: .3s;
        -ms-transition: .3s;
        -o-transition: .3s;

        &:hover {
            opacity: .8;
        }
    }
}

.col-12.ourcont-wrap {
    padding-left: 0;
    padding-right: 0;
}

.ourcontacts {
    padding: 164px 15px 90px 15px;
    background-color: $black;
    margin-top: -111px;
    z-index: 2;
    background-image: url('../img/bg7.png');
    background-repeat: repeat;

    .title {
        color: $white;
        font-size: 30px;
        line-height: 48px;
        font-family: Rawercondensedbold;
        margin: 0 0 10px 0;
    }

    .ourcontacts__subtitle {
        margin: 40px 0 8px 0;
        color: #828282;
        font-size: 14px;
        line-height: 24px;
        font-family: Rawercondensedregular;
    }

    .ourcontacts__subdescr {
        text-decoration: none;
        outline: none;
        color: $white;
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;

    }
}




@media(min-width:576px) {
    .contact-topsect .container {
        text-align: center;
    }

    .contact-topsect h1 {
        max-width: 540px;
        display: inline-block;
        text-align: left;
        margin-left: 30px;
    }

    .caontactform,
    .ourcontacts-wr {
        max-width: 510px;
        display: inline-block;
        width: 100%;
    }

    .ourcontacts-wr {
        text-align: left;
    }
}

@media(min-width:1025px) {
    .contact-topsect .container {
        max-width: 960px;
        padding-left: 15px;
        padding-right: 15px;
    }

    .contact-topsect .col-12 {
        flex: 0 0 62%;
        max-width: 62%;
    }

    .contact-topsect .col-12.ourcont-wrap {
        flex: 0 0 38%;
        max-width: 38%;
        position: initial;
    }

    .contact-topsect .container .row {
        margin-left: -15px;
        margin-right: -15px;
    }

    .contact-topsect h1 {
        max-width: 100%;
        margin-left: 0;
    }

    .contact-topsect {
        padding-top: 132px;
    }

    .contact-topsect h1 {
        line-height: 64px;
        margin-bottom: 64px;
    }

    .caontactform,
    .ourcontacts-wr {
        max-width: 100%;
    }

    .caontactform {
        padding: 68px 122px 48px 122px;
        text-align: left;
    }

    .caontactform .inputfield textarea {
        height: 90px;
    }

    .caontactform .inputfield input {
        margin-bottom: 22px;
    }

    .caontactform .inputfield textarea {
        margin-bottom: 35px;
    }

    .caontactform input[type=submit] {
        max-width: 278px;
        margin: 0 auto;
    }

    .ourcontacts {
        // position: relative;
        background-color: transparent;
        background-image: none;

        &:after {
            content: '';
            display: block;
            position: absolute;
            left: 48%;
            bottom: 0;
            top: initial;
            background-color: $black;
            background-image: url('../img/contbg.png');
            background-repeat: repeat;
            width: 100%;
            height: 75vh;
            background-size: cover;
        }
    }

    .ourcontacts-wr {
        z-index: 2;
        position: relative;
    }

    .contus-main {
        position: relative;
        overflow: hidden;
    }


    a.ourcontacts__subdescr {
        color: #fff;
        transition: .3s;
        -webkit-transition: .3s;
        -moz-transition: .3s;
        -ms-transition: .3s;
        -o-transition: .3s;
    }

    a.ourcontacts__subdescr:hover {
        color: #aeccc2;
    }
}










@media(min-width:1200px) {
    .contact-topsect .container {
        max-width: 1140px;
    }

}

