.visitslide-wrap .swiper-slide {
    min-height: 100px;
}

.visitslide-wrap {
    overflow: hidden;
}

.visitslide-wrap .swiper-wrapper {
    flex-wrap: wrap;
}

.visitslide-wrap .swiper-button-next,
.visitslide-wrap .swiper-button-prev,
.visitslide-wrap .swiper-pagination {
    display: none;
}

.visitinf {
    padding: 44px 0 0 0;

    h1 {
        margin-bottom: 30px;
    }

    .descr {
        color: $black;
        font-size: 16px;
        line-height: 24px;
        margin: 0;
        padding-left: 26px;
        font-family: Rawercondensedregular;
    }

}

.visslidewrap {
    position: relative;

    img {
        width: 100%;
        margin-bottom: 20px;
    }
}

.visitslide-wrap {
    margin-top: 36px;
}

.googlelink {
    width: 300px;
    height: 100px;
    background: rgba(0, 0, 0, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 2;
    top: 19px;
    left: 0;
    right: 0;
    margin: 0 auto;
    color: $white;

    &:after {
        content: '';
        display: inline-block;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        background: rgba(51, 51, 51, 0.8);
        position: absolute;
        left: 10px;
        top: 10px;
    }

    span {
        position: relative;
        z-index: 2;
        font-size: 20px;
        line-height: 24px;
        font-family: Rawercondensedbold;
    }
}

.googlemap {
    margin-top: 15px;

    .title {
        margin: 0 0 46px 0;
        padding-left: 29px;
        color: $black;
        font-size: 20px;
        line-height: 28px;
        font-family: Rawercondensedbold;
        max-width: 250px;
    }

    .mappiclink {
        display: block;
        height: 485px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 50% 50%;
        }
    }
}

.desckmappic {
    display: none;
}

.vworktime-title {
    margin: 0 0 14px 0;
    font-size: 20px;
    line-height: 24px;
    color: #000;
    font-family: Rawercondensedbold;
    padding-left: 24px;
}

.vworktime-list {
    list-style-type: none;
    padding: 0 0 0 24px;
    margin: 0;

    li {
        font-size: 16px;
        line-height: 24px;
        font-family: Rawercondensedregular;
        color: #000;
    }
}

@media(min-width:576px) {
    .mobmappic {
        display: none;
    }

    .desckmappic {
        display: block;
    }

    .googlemap .mappiclink {
        height: 300px;
    }
}

@media(min-width:768px) {
    .visitslide-wrap .swiper-slide {
        max-width: 50%;
        flex: 0 0 50%;
        padding: 0 15px;
        box-sizing: border-box;
    }

    .visslidewrap {
        height: 300px;
        margin-bottom: 30px;

        img {
            height: 100%;
            object-fit: cover;
            object-position: 50% 50%;
        }
    }
}

@media(min-width:1025px) {
    .visitslide-wrap .swiper-slide {
        max-width: initial;
        flex: none;
        padding: 0;
        box-sizing: border-box;
    }

    .visslidewrap {
        height: initial;
        margin-bottom: 0;

        img {
            height: initial;
            object-fit: none;
            object-position: initial;
        }
    }

    .visitslide-wrap .swiper-wrapper {
        flex-wrap: nowrap;
    }

    .visitslide-wrap .swiper-slide {
        min-height: initial;
    }

    .visslidewrap {
        // width: 100%;

        img {
            margin-bottom: 0;
            width: auto;
            max-width: 100%;
        }
    }

    .swiper-containerv .swiper-container {
        width: 100%;
        height: 100%;
    }

    .swiper-containerv .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;

        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }

    .swiper-containerv .swiper-slide {
        width: auto;
    }

    .visslidewrap {
        height: 600px;
        overflow: hidden;
        transition-delay: inherit !important;
        opacity: 1 !important;
        transform: none !important;
        -webkit-transform: none !important;
        -moz-transform: none !important;
        -ms-transform: none !important;
        -o-transform: none !important;
        transition-duration: inherit !important;
        transition-timing-function: inherit !important;
        transition-property: none !important;

        img {
            height: 100%;
        }
    }

    .visitinf .swiper-wrapper {
        padding-left: 15px;
    }

    .visitinf {
        padding-top: 120px;
    }

    h1 {
        font-size: 48px;
        line-height: 58px;
        padding-left: 30px;
        border: none;
        margin-bottom: 110px;


    }

    .visitinf .descr {
        font-size: 24px;
        line-height: normal;
    }

    .visitslide-wrap {
        margin-top: 50px;
        padding-bottom: 100px;
    }

    .googlelink {
        right: initial;
        top: initial;
        left: 10px;
        bottom: 10px;
        width: 260px;
        height: 70px;
        transition: .5s;

        &:hover {
            background: rgba(0, 0, 0, .7);
            color: #68b59a;

            &:after {
                background: rgba(51, 51, 51, .7);
            }
        }
    }

    .visitslide-wrap .swiper-button-next,
    .visitslide-wrap .swiper-button-prev,
    .visitslide-wrap .swiper-pagination {
        display: inline-block;
    }

    .swiper-containerv {
        padding-bottom: 62px;
    }

    .visitslide-wrap .swiper-pagination {
        bottom: 0;
        position: absolute;
        transform: none;
    }

    .googlemap {
        margin-top: 120px;
    }

    .swiper-pagination-bullet {
        margin: 0 10px;
        background: rgba(1, 1, 1, 0.5);
        width: 12px;
        height: 12px;
        outline: none;
    }

    .swiper-pagination-bullet-active {
        background: $black;
    }

    .swiper-containerv {
        position: relative;
    }

    .visitslide-wrap .container {
        max-width: 100%;
    }

    .visitslide-wrap .swiper-button-next,
    .visitslide-wrap .swiper-button-prev {
        width: 50px;
        height: 96px;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            width: 19px;
            height: auto;

            rect {
                transition: .3s;
            }
        }

        &:hover {
            svg {
                rect {
                    fill: #AECCC2;
                }
            }
        }
    }

    .visitslide-wrap .swiper-button-prev:after,
    .visitslide-wrap .swiper-button-next:after {
        display: none;
    }

    .googlemap .title {
        font-size: 24px;
        line-height: normal;
        max-width: initial;
    }

    .googlemap .mappiclink {
        height: 400px;
    }







}

@media(min-width:1025px) and (max-width:1300.8px) {
    .visslidewrap {
        height: 400px;

        img {
            height: 100%;
        }
    }
}

